//If Number(this.hourChange) = 0 css color blue
//If Number(this.hourChange) > 0 css color green
//If Number(this.hourChange) < 0 css color red
export default Vue.extend({
  props: {
    coinName: String,
    coinSymbol: String,
    currentPrice: String,
    hourChange: String,
    dailyChange: String,
    marketDat: Array
  },

  data: () => ({

      width: 2,
      radius: 3,
      padding: 8,
      lineCap: 'round',
      gradient: ['#00c6ff', '#F0F', '#FF0'],
      //value: [0, 2, 5, 9, 5, 10, 3, 5, 0, 0, 1, 8, 2, 9, 0],
      gradientDirection: 'top',
      //gradients,
      fill: true,
      type: 'trend',
      autoLineWidth: false,
      testArr: [0, 2, 5, 9, 5, 10, 3, 5, 0, 0, 1, 8, 2, 9, 0],
    }),


  methods: {
    test(){
      console.log("CREATED");
      console.log(this.testArr);
    },

    colorizer(item: string){
      var num = Number(item.substring(0, item.length - 1));
      console.log(num);
      if(num == 0){
        return "color: #FAA613"
      } if(num > 0){
        return "color: #688E26"
      }if (num < 0){
        return "color: #FAB3A9"
      }
    },

    created(){
            //this.testArr = this.marketDat;
    }

  }

});
